.m-movies{
    width: auto;
    margin-left: 7.2vmax;
    left: 3.5vw;
    display: grid;
    grid-template-columns: repeat(7, 11.7%);
    column-gap: 30px;
    row-gap: 30px;
    align-content: center;
}

.m-movies::-webkit-scrollbar{    
    display: none;
}

.not-found{
    position: relative;
    width: 85.6vw;
    display: flex;
    justify-content: center;
}
